import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import moment from 'moment';
import compose from 'recompose/compose';
import withProps from 'recompose/withProps';

/** local components */
import NewsBlock from '../components/NewsBlock';

/** News Posts */
import NewsPost from './news/2018-02-05';

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const News = ({ articles }) => {
  if (!articles.length) {
    return (
      <Container>
        <NewsBlock
          title="沒有文章"
        >
          <h1>不然你來新增一篇文章？</h1>
        </NewsBlock>
      </Container>
    );
  }
  return (
    <Container>
      {articles.map((article, index) => (
        <NewsBlock
          key={index}
          title={article.title}
          btnLink={article.link}
          btnText={article.btnText}
          timeStamp={moment(article.time).format('YYYY/MM/DD')}
        >
          {article.content}
        </NewsBlock>
      ))}
    </Container>
  );
}

News.propTypes = {
  articles: PropTypes.arrayOf(PropTypes.object),
}

News.defaultProps = {
  articles: []
}

export default compose(
  withProps(() => ({
    articles: [
      {
        title: '超值火鍋店取/宅配 (3～4人份)',
        time: Date.now(),
        content: <NewsPost />,
        link: '/menu/',
        btnText: '觀看菜單'
      }
    ]
  }))
)(News);



